    cursor: pointer;
    font-size: 24px;
    padding-top: 7px;
}

.close-btnRV>svg {
    width: 25px;
}

/* 리뷰 박스, 아이템 등은 그대로 유지 */

.review-headerRV {
    display: flex;
    justify-content: space-between;
    /* 작성자 왼쪽, 날짜 오른쪽 */
    align-items: center;
    margin: 5px 0px 10px 30px;
