}

.reviewboxallRV {
    flex: 1;
    overflow-y: auto;
    padding: 0 20px 20px 20px;
}

.review-itemRV {
    display: flex;
    align-items: flex-start;
    justify-content: space-between;
    padding: 10px 0;
    border-bottom: 1px solid #eee;
}

.review-textRV {
    flex: 1;
    color: #6C5242;
}

