.review-authorRV {
    font-weight: bold;
    font-size: 18px;
    margin: 0;
}

.review-footerRV {
    display: flex;
    justify-content: space-between;
    /* 날짜 왼쪽, 버튼 오른쪽 */
    align-items: center;
    /* 세로 기준으로 중앙 정렬 */
