    /* 필요에 따라 조정 */
}


.review-actionsRV button {
    padding: 2px 6px;
    font-size: 12px;
    cursor: pointer;
}

.review-itemRV {
    display: flex;
    gap: 12px;
    padding: 10px 0;
    border-bottom: 1px solid #eee;
}

.review-textRV {
    flex: 1;
    display: flex;
    flex-direction: column;
}

.review-topRV {
    display: flex;
    justify-content: flex-end;
    /* 날짜 오른쪽 */
    margin-bottom: 5px;
}

/* 날짜 스타일 */
.review-dateRV {
    font-size: 14px;
    color: #999;
}

.review-actionsRV {
    display: flex;
    gap: 8px;
    justify-content: flex-end;
    /* 맨 아래 오른쪽 정렬 */
    margin-top: auto;
    /* 내용 끝까지 밀어내고 아래 붙이기 */
}


.edit-btnRV,
.delete-btnRV {
    color: #999;
}

.edit-btnRV:hover,
.delete-btnRV:hover {
    background-color: #6C5242;
    color: #ffffff;
}





.review-inputRV {
    display: flex;
    padding: 5px 20px 30px 20px;
    align-items: flex-start;
    margin-top: 10px;
    flex-direction: column;
    flex-wrap: nowrap;
}

.review-userRV {
    padding-left: 197px;
    width: 100%;
    height: 10px;
    display: flex;
    align-items: center;

    color: #6C5242;
    font-weight: 700;
    font-size: 18px;
}

.review-conRV {
    display: flex;
    gap: 25px;
    width: 100%;
    align-items: end;
    position: relative;
}

.file-boxRV {
    width: 170px;
    height: 253px;
    gap: 10px;
    display: flex;
    align-items: center;
    flex-direction: column;
    position: absolute;
    /* top: 70; */
}

.file-infoRV {
    width: 147px;
    height: 132px;
    /* 원하는 높이 */
    border: 1px solid #6C5242;
    font-size: 14px;
    font-weight: 400;
    color: #999;

    /* padding 제거 */
    padding: 0;

    text-align: center;
    display: flex;
    align-items: center;
    /* 수직 가운데 */
    justify-content: center;
    /* 가로 가운데 */

    margin-top: 30px;
    transform: translateY(15px);
    /* 원하는 만큼 아래로 */

}

.file-btnRV {
    width: 130px;
    height: 50px;
    background-color: #6C5242;
    color: #FFFEF8;
    border-radius: 10px;
    cursor: pointer;
    font-size: 22px;
    font-weight: 500;
    text-align: center;
    line-height: 50px;
    margin-top: 30px;
}

.text-area-wrap {
    display: flex;
    flex-direction: column;
    margin-left: 192px;
}

.text-area-wrap textarea {
    width: 1186px;
