    font-family: "Hahmlet", serif;
    margin-bottom: 10px;
    font-size: 16px;
    font-weight: 500;
}

.submit-btnRV {
    width: 150px;
    height: 50px;
    background-color: #6C5242;
    color: #FFFEF8;
    border-radius: 10px;
    font-size: 22px;
    font-weight: 500;
    cursor: pointer;
    align-self: flex-end;
    margin-top: 15px;
}

.review-paginationRV {
    margin: 5px 0 0 0;
    text-align: center;
}

.review-paginationRV button {
    margin: 0 3px;
    padding: 3px 8px;
    cursor: pointer;
    color: #6C5242;
    background-color: transparent;
    border: none;
    outline: none;
}

.review-paginationRV button.active {
    color: #6C5242;
    font-weight: 800;
}

.review-itemRV img {
    width: 149px;
    height: 133px;
    object-fit: cover;
}


.star-ratingRV {
    display: flex;
    justify-content: flex-start;
    /* 왼쪽 정렬 */
    gap: 6px;
    margin: 10px 0;
    /* 위아래 간격 */

}

.star-ratingRV .star {
    font-size: 24px;
    color: #ccc;
    cursor: pointer;
    transition: color 0.2s ease-in-out;
}

.star-ratingRV .star:hover,
.star-ratingRV .star.filled {
    color: #ffcc00;
    /* 노란색으로 채워짐 */
}



/* 댓글 쓰는 창 별 */

.star {
    color: #ccc;
    font-size: 16px;
    margin-right: 2px;
}

.star.filled {
    color: #ffc107;
