/* 뉴스 목록 컨테이너 */
.news-list-block {
    box-sizing: border-box;
    padding-bottom: 3rem;
    max-width: 1440px;
    /* 반응형을 위해 width → max-width */
    width: 100%;
    margin: 0 auto;
    display: flex;
    flex-direction: column;
    gap: 2rem;
}

/* 반응형 - 모바일 화면일 때 */
@media screen and (max-width: 768px) {
    .news-list-block {
        padding-left: 1rem;
        padding-right: 1rem;
    }
}

/* 카테고리 버튼 영역 */
.category-buttons {
    display: flex;
    flex-wrap: wrap;
    gap: 10px;
    justify-content: center;
    margin-bottom: 1rem;
}

/* 기본 카테고리 버튼 스타일 */
.category-btn {
    width: 95px;
    height: 35px;
    border: 1px solid #6C5242;
    background-color: transparent;
    border-radius: 8px;
    font-size: 18px;
    font-weight: 500;
    color: #6C5242;
    font-family: "Hahmlet", serif;
    text-align: center;
    line-height: 35px;
    cursor: pointer;

    margin-bottom: 10px;
}

/* 클릭 시(active) 스타일 */
.category-btn.active {
    background-color: #6C5242;
    color: #ffffff;
}

/* 뉴스 아이템을 2컬럼으로 표시 */
.news-list-grid {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    /* 두 칼럼 */
    gap: 1.5rem;
    width: 100%;
}

/* 모바일에서는 1컬럼 */
@media screen and (max-width: 768px) {
    .news-list-grid {
        grid-template-columns: 1fr;
    }
}